body {
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f('ub300', 16, 26);
   color: $color4;
   &.is-locked {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      overflow: hidden; } }

.container {
   position: relative;
   z-index: 2;
   max-width: 1140px;
   margin: 0 auto;
   padding: 0 15px; }

.row {
   display: flex;
   margin: 0 -15px; }

.col {
   padding: 0 15px; }

.main {
   position: relative;
   display: block;
   overflow: hidden;
   &::before, &::after {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 1px;
      background-color: rgba(#e6e6e6, .5);
      content: ''; }
   &::before {
      left: 10.4%; }
   &::after {
      right: 10.4%; } }

.section {
   position: relative;
   @include xs {
      padding: 60px 0; } }

.section-title {
   $parent: &;
   &_center {
      text-align: center; }
   &_left {
      text-align: left; }
   &__text {
      position: relative;
      padding-bottom: 16px;
      margin-bottom: 13px;
      @include f('ub700', 45, 45);
      color: $color1;
      @include xs {
         font-size: 42px;
         line-height: 42px; }

      #{$parent}_lg & {
         margin-bottom: 22px;
         padding-bottom: 23px;
         text-transform: uppercase;
         font-size: 120px;
         line-height: 100px;
         @include md {
            padding-bottom: 10px;
            margin-bottom: 15px;
            font-size: 100px;
            line-height: 80px; } }

      #{$parent}_left & {
         &::after {
            left: 0;
            content: ''; } }
      #{$parent}_center & {
         &::after {
            left: calc(50% - 25px);
            content: ''; } }

      &::after {
         position: absolute;
         bottom: 0;
         width: 50px;
         height: 2px;
         background-color: $color5;
         opacity: 0.5;
         content: ''; } }

   &__descr {
      font-size: 22px;
      line-height: 32px;
      color: $color3;
      @include md {
         font-size: 18px;
         line-height: 28px; }

      strong, b {
         @include f('ub500'); } } }

.btn {
   position: relative;
   display: inline-block;
   padding: 18px 35px;
   min-width: 220px;
   outline: 0;
   @include f('ub500', 16, 20);
   text-align: center;
   cursor: pointer;
   box-shadow: 0 10px 30px rgba(214, 77, 135, 0.4);
   border-radius: 4px;
   color: $white;
   background-color: $color5;
   transition: .4s cubic-bezier(0.5, 0.89, 0.32, 1.5);
   &::before, &::after {
      position: absolute;
      top: calc(50% - 18px);
      z-index: 1;
      width: 14px;
      height: 36px;
      opacity: 0;
      background-repeat: no-repeat;
      border: 2px solid rgba($white, .2);
      transition: .4s cubic-bezier(0.5, 0.89, 0.32, 1.5);
      content: ''; }
   &::before {
      left: 12px;
      border-right: 0;
      transform: translateX(-12px); }
   &::after {
      right: 12px;
      border-left: 0;
      transform: translateX(12px); }

   &:hover {
      &::before, &::after {
         opacity: 1;
         transform: translateX(0); } }
   &:active {
      box-shadow: none;
      background-color: darken($color5, .5); } }

.jelly-decor {
   position: absolute;
   z-index: 1;
   pointer-events: none; }

.decor-dots {
   $parent: &;
   @include coverdiv;
   z-index: 3;
   pointer-events: none;
   &__el {
      &::before, &::after {
         position: absolute;
         width: 4px;
         height: 4px;
         border-radius: 50%;
         background-color: $color1;
         opacity: 0.2;
         content: ''; }
      &_top {
         &::before {
            top: -2px;
            left: -2px; }
         &::after {
            top: -2px;
            right: -2px; }
         #{$parent}_support &::before {
            top: -1px;
            left: -1px; }
         #{$parent}_support &::after {
            top: -1px;
            right: -1px; }
         &::after {
            top: -2px;
            right: -2px; }
         #{$parent}_block &::before {
            top: 15px;
            left: 15px; }
         #{$parent}_block &::after {
            top: 15px;
            right: 15px; } }
      &_bot {
         &::before {
            bottom: -2px;
            left: -2px; }
         &::after {
            bottom: -2px;
            right: -2px; }
         #{$parent}_support &::before {
            bottom: -1px;
            left: -1px; }
         #{$parent}_support &::after {
            bottom: -1px;
            right: -1px; }
         #{$parent}_block &::before {
            bottom: 15px;
            left: 15px; }
         #{$parent}_block &::after {
            bottom: 15px;
            right: 15px; } } } }

.text-label {
   position: relative;
   padding: 14px 20px;
   border-radius: 4px;
   border: 2px solid rgba(#d6d1de, .1);
   background-color: $white;
   text-align: center;
   text-transform: uppercase;
   @include f('ub500', 16, 24);
   color: $color1;
   span {
      color: $color5; } }

.section-back-text {
   position: absolute;
   z-index: 2;
   @include f('ub700', 100, 70);
   text-transform: uppercase;
   white-space: nowrap;
   color: rgba(#341b5c, .03);
   @include md {
      font-size: 70px; }
   @include xs {
      font-size: 40px; } }

