// *** begin squared-list *** //
.squared-list {
   &__item {
      position: relative;
      &:not(:last-child) {
         margin-bottom: 25px;
         @include md {
            margin-bottom: 15px; } }
      &::before {
         position: absolute;
         top: 8px;
         left: -20px;
         width: 6px;
         height: 6px;
         border: 2px solid $color5;
         transform: rotate(45deg);
         content: ''; }

      &-title {
         margin-bottom: 2px;
         @include f('ub500', 18, 24);
         color: $color3; } } }
// end squared-list //
