// *** begin block-1 *** //
.block-1 {
   padding: 210px 0 110px;
   @include xs {
      height: 100vh;
      padding: 100px 0 0; }
   @include mob-fix {
      height: 160vh; }
   &__container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      max-width: 1180px;
      @include md {
         align-items: center; }
      @include xs {
         height: 100%; } }
   &__jelly {
      &_1 {
         width: 57.4%;
         top: -11%;
         right: -5.8%;
         @include lg {
            width: 66.4%; }
         @include md {
            width: 76.4%; }
         @include xs {
            width: 82%; } }
      &_2 {
         width: 60.6%;
         top: -28.9%;
         left: -22.7%;
         @include md {
            width: 65.6%;
            top: -3%;
            left: -14.5%; } } }
   &__content {
      padding: 25px 0 0 60px;
      flex: 0 1 percentage(450 / 1150);
      width: percentage(450 / 1150);
      @include md {
         flex: 0 1 54%;
         width: 54%;
         padding: 0; }
      @include xs {
         flex: 0 1 auto;
         width: 100%; } }
   &__title {
      margin-bottom: 30px; }
   &__img-wrap {
      flex: 0 1 percentage(680 / 1150);
      width: percentage(680 / 1150);
      @include ratio(680, 387);
      @include cover-img;
      @include xs {
         display: none; }
      img {
         @include coverdiv;
         width: 100%; } }
   &__back-text {
      top: 41%;
      left: -14.8%;
      font-size: 270px;
      transform: rotate(-90deg);
      @include lg {
         font-size: 200px;
         top: 29%;
         left: -15.8%; }
      @include md {
         font-size: 100px;
         top: 31%;
         left: -13.8%; }
      @include xs {
         left: -33.8%; } } }
// end block-1 //

// *** begin block-2 *** //
.block-2 {
   padding: 90px 0;
   &__back-text {
      right: -6.4%;
      bottom: 3%;
      @include xs {
         bottom: 0;
         right: 0; } }
   &__jelly {
      width: 50.4%;
      right: -16%;
      top: 94%; }
   &__container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      max-width: 1170px;
      @include md {
         align-items: center; } }
   &__img-wrap {
      flex: 0 1 48.18919%;
      width: 48.18919%;
      @include ratio(546, 312);
      @include cover-img;
      @include xs {
         display: none; }
      img {
         @include coverdiv;
         width: 100%; } }
   &__content {
      padding: 30px;
      flex: 0 1 percentage(570 / 1110);
      width: percentage(570 / 1110);
      @include md {
         padding: 0 0 0 10px;
         flex: 0 1 53%;
         width: 53%; }
      @include xs {
         flex: 0 1 auto;
         width: 100%;
         padding: 0; } }
   &__title {
      margin-bottom: 30px;
      @include md {
         margin-bottom: 10px; } } }
// end block-2 //

// *** begin block-3 *** //
.block-3 {
   padding: 60px 0 50px;
   &__back-text {
      bottom: 0;
      left: -1%; }
   &__container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      @include md {
         align-items: center; } }
   &__content {
      flex: 0 1 percentage(445 / 1110);
      width: percentage(445 / 1110);
      padding: 25px 0 0 35px;
      @include md {
         padding: 0 10px 0 20px;
         flex: 0 1 61%;
         width: 61%; }
      @include xs {
         flex: 0 1 auto;
         width: 100%;
         padding: 0; } }

   &__img-wrap {
      margin: 25px 25px 0 0;
      flex: 0 1 52.63063%;
      width: 52.63063%;
      @include ratio(562, 422);
      @include cover-img;
      @include md {
         margin: 25px 0 0; }
      @include xs {
         display: none; }
      img {
         @include coverdiv;
         width: 100%; } }
   &__title {
      margin-bottom: 30px; }
   &__list {
      @include xs {
         padding-left: 20px; } } }
// end block-3 //

// *** begin pricing *** //
.pricing {
   padding: 97px 0 90px;
   &__jelly {
      width: 43.3%;
      top: -4%;
      left: -13.5%;
      @include md {
         width: 70.3%;
         top: 29%;
         left: -28.5%; }
      @include xs {
         top: 2%;
         left: -42.5%; } }
   &__back-text {
      bottom: -3%;
      right: -1%; }
   &__title {
      margin-bottom: 30px; }
   &__container {}
   &__row {
      margin: 0;
      align-items: center;
      justify-content: space-between;
      @include xs {
         flex-direction: column; } }
   &__col {
      &:nth-child(1) {
         flex: 0 1 percentage(528 / 1110);
         @include xs {
            flex: 0 1 auto;
            width: 100%; } }
      &:nth-child(2) {
         flex: 0 1 percentage(540 / 1110);
         margin-top: -5px;
         padding: 0 10px;
         @include md {
            flex: 0 1 60%;
            width: 60%;
            padding: 0 0 0 20px; }
         @include xs {
            flex: 0 1 auto;
            width: 100%;
            padding: 30px 0 0; } }
      > p {
         margin-bottom: 25px; } }
   &__block {
      position: relative;
      height: 180px;
      overflow: hidden;
      box-shadow: 0 10px 40px rgba(61, 61, 61, 0.07);
      border-radius: 4px;
      background-color: $white;
      &::before, &::after {
         position: absolute;
         z-index: 1;
         width: 212px;
         height: 130px;
         border-radius: 50%;
         background-color: #f5f3f7;
         content: ''; }
      &::before {
         left: -50px;
         top: calc(100% - 31px); }
      &::after {
         right: -43px;
         bottom: calc(100% - 28px); }
      &-inner {
         @include flexCenter;
         height: 100%;
         &::before, &::after {
            position: absolute;
            top: 50%;
            z-index: 2;
            transform: translateY(-50%);
            width: 81px;
            height: 44px;
            background: url("../img/decor/vertical-lines.svg");
            content: ''; }
         &::before {
            left: 7px; }
         &::after {
            right: 7px; } }
      &-price {
         margin-left: 35px;
         margin-right: 10px;
         @include f('ub700', 110, 80);
         color: $color1;
         @include md {
            font-size: 80px; }
         @include xs {
            font-size: 64px; } }
      &-details {
         flex: 0 0 110px; }
      &-text {
         position: relative;
         z-index: 2;
         display: flex;
         align-items: flex-start;
         justify-content: flex-start; }
      &-val {
         margin: 5px 0 15px;
         color: $color1;
         font-size: 40px; }
      &-info {
         @include f('ub300i');
         line-height: 18px;
         color: $color4;
         span {
            @include f('ub500i');
            color: $color5; } } }
   &__actions {
      display: flex;
      align-items: center;
      @include xs {
         flex-direction: column;
         align-items: center; }
      &-btn {
         margin-right: 30px;
         @include md {
            min-width: 165px;
            padding: 18px 15px;
            margin-right: 20px; }
         @include xs {
            min-width: 220px;
            margin: 0 0 25px; } }
      &-label {
         flex: 1;
         @include md {
            padding: 14px 10px;
            font-size: 15px; }
         @include xs {
            min-width: 220px; } } } }
// end pricing //

// *** begin features *** //
.features {
   padding: 105px 0;
   @include md {
      padding: 75px 0; }
   &__jelly {
      &_1 {
         top: 26.5%;
         left: -6.9%;
         @include md {
            top: 0.5%;
            left: -13.9%; } }
      &_2 {
         width: 48.7%;
         top: 31.5%;
         right: -22.7%;
         @include lg {
            width: 43.7%;
            top: 61.5%; }
         @include md {
            top: 61.5%;
            right: -52.7%;
            width: 73.3%; } } }

   &__back-text {
      bottom: 0.7%;
      left: -0.5%;
      @include xs {
         bottom: -20px; } }
   &__title {
      margin-bottom: 30px; }
   &__container {
      z-index: 3; }
   &__row {
      flex-wrap: wrap;
      counter-reset: featuresCount;
      @include md {
         justify-content: center; }
      @include xs {
         flex-direction: column;
         align-items: center;
         margin: 0; } }
   &__item {
      $parent: &;
      position: relative;
      flex: 0 1 calc(33.33333% - 30px);
      width: calc(33.33333% - 30px);
      height: 150px;
      margin: 0 15px;
      padding: 0 70px 0 40px;
      cursor: default;
      counter-increment: featuresCount;
      @include sm {
         flex: 0 1 calc(50% - 30px);
         width: calc(50% - 30px); }
      @include xs {
         flex: 0 1 auto;
         width: 100%;
         max-width: 350px;
         cursor: pointer; }
      &::after {
         @include coverdiv;
         z-index: -1;
         opacity: 0;
         pointer-events: none;
         background: $white;
         border-radius: 4px;
         box-shadow: 0 10px 40px rgba(61, 61, 61, 0.2);
         transition: .3s ease-in-out;
         content: ''; }
      &::before {
         position: absolute;
         top: 40px;
         right: 38px;
         color: $color5;
         @include f('ub700', 14, 14);
         content: counter(featuresCount, decimal-leading-zero);
         @include xs {
            top: 20px;
            right: 20px;
            pointer-events: none; } }
      &:hover {
         &::after {
            opacity: 1;
            transform: scale(1.086, 1.173); } }
      &:nth-child(1) ~ & {
         @include xs {
            margin-top: 30px; } }
      &:nth-child(2) ~ & {
         @include sm {
            margin-top: 30px; } }
      &:nth-child(3) ~ & {
         margin-top: 30px; }

      &-inner {
         display: flex;
         z-index: 2;
         align-items: center;
         justify-content: space-between;
         height: 100%;
         #{$parent}:hover &::after {
            transform: scaleX(1); }
         &::after {
            position: absolute;
            top: calc(50% - 22px);
            right: 0;
            z-index: 2;
            width: 81px;
            height: 44px;
            transition: .4s ease-in-out;
            background: url("../img/decor/vertical-lines.svg");
            transform-origin: 75% 100%;
            transform: scaleX(0);
            content: ''; } }
      &-text {
         position: relative;
         flex: 0 0 170px;
         @include xs {
            padding-right: 20px; } }
      &-title {
         position: relative;
         @include f('ub500', 18, 24);
         color: $color3;
         @include xs {
            font-size: 16px; }
         &::before {
            position: absolute;
            top: 10px;
            left: -12px;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: $color5;
            opacity: 0.2;
            content: ''; } }
      p {
         color: $color4;
         font-size: 16px; }
      &-icon-wrap {
         position: relative;
         #{$parent}:nth-child(3) &::after {
            right: calc(100% + 18px);
            @include xs {
               right: calc(100% + 4px); } }
         #{$parent}:nth-child(3) &::before {
            left: -28px; }
         #{$parent}:nth-child(7) &::before {
            top: -7px;
            left: -20px; }
         &::before {
            position: absolute;
            top: 3px;
            left: -20px;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: $color1;
            opacity: 0.05;
            content: ''; }
         &::after {
            position: absolute;
            top: 50%;
            right: calc(100% + 10px);
            width: 40px;
            height: 2px;
            background-color: rgba($color1, .5);
            content: ''; } }
      &-decor {
         #{$parent}:hover &::before,
         #{$parent}:hover &::after {
            border-color: $color1;
            opacity: 0.3; }
         &::before, &::after {
            position: absolute;
            width: 14px;
            height: 14px;
            border: 2px solid #e6e6e6;
            opacity: 0.5;
            transition: .3s ease-in-out;
            content: ''; }
         &_top {
            &::before {
               top: -2px;
               left: -2px;
               border-bottom: 0;
               border-right: 0; }
            &::after {
               top: -2px;
               right: -2px;
               border-left: 0;
               border-bottom: 0; } }
         &_bot {
            &::before {
               bottom: -2px;
               left: -2px;
               border-top: 0;
               border-right: 0; }
            &::after {
               bottom: -2px;
               right: -2px;
               border-left: 0;
               border-top: 0; } } } } }
// end features //

// *** begin supported *** //
.supported {
   padding: 94px 0 152px;
   &__back-text {
      bottom: 7.5%;
      right: 1.4%; }
   &__jelly {
      width: 41.2%;
      top: -2.3%;
      left: -10.3%;
      @include md {
         width: 71.2%;
         left: -50.3%; } }
   &__title {
      margin-bottom: 28px; }
   &__row {
      margin: 0 -24px;
      @include md {
         flex-direction: column; }
      &:not(:last-child) {
         margin-bottom: 35px; } }
   &__block {
      $parent: &;
      position: relative;
      padding: 0 24px;
      &:not(:last-child) {
         @include md {
            margin-bottom: 30px; }
         &::after {
            position: absolute;
            right: -1px;
            bottom: 27px;
            width: 2px;
            height: 62px;
            background-color: $color1;
            opacity: 0.06;
            content: '';
            @include md {
               display: none; } } }
      &-title {
         margin-bottom: 14px;
         @include f('ub700', 20, 24);
         text-transform: uppercase;
         color: $color1;
         text-align: center;
         #{$parent}_sm & {
            margin-bottom: 5px;
            @include md {
               margin-bottom: 14px; } } }
      &-row {
         display: flex;
         justify-content: center;
         flex-wrap: wrap;
         margin: 0 -11px; }
      &-item {
         $parent: &;
         position: relative;
         @include flexCenter;
         flex: 0 0 100px;
         width: 100px;
         height: 100px;
         margin: 11px;
         background-color: $white;
         border-radius: 4px;
         &::before {
            @include coverdiv;
            z-index: -1;
            opacity: 0;
            box-shadow: 0 10px 40px rgba(61, 61, 61, 0.2);
            border-radius: 4px;
            background-color: $white;
            transition: .3s ease-in-out;
            content: ''; }
         &::after {
            @include coverdiv;
            border: 2px solid #d6d1de;
            border-radius: 4px;
            transition: .3s ease-in-out;
            content: ''; }
         span {
            @include f('ub400', 18, 24);
            color: $color3;
            transition: .3s ease-in-out; }
         &:hover {
            span {
               color: $color5; }
            &::before {
               opacity: 1;
               transform: scale(1.2); }
            &::after {
               opacity: 0.1; }
            .decor-dots {
               opacity: 1; } }
         .decor-dots {
            opacity: 0;
            transition: .3s ease-in-out; }
         &_sm {
            flex: 0 0 80px;
            width: 80px;
            height: 80px;
            margin: 23px;
            @include md {
               margin: 11px; }
            &::after {
               border-color: #f8dbe4; }
            .decor-dots {
               &__el {
                  &::after, &::before {
                     background-color: $color5; } } } } } } }
// end supported //
